.layout {
  min-height: 100vh;
  background: #f5f5f5;
}

.header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 0;
  height: auto;
  line-height: normal;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 24px 48px;
}

.content {
  padding: 60px 24px;
}

.content-wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.intro-section {
  text-align: center;
  margin-bottom: 60px;
  padding: 0 24px;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}

.main-title {
  font-size: 48px !important;
  font-weight: 700 !important;
  margin-bottom: 24px !important;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.main-description {
  font-size: 18px !important;
  color: rgba(0, 0, 0, 0.65) !important;
  margin-bottom: 0 !important;
  line-height: 1.8 !important;
}

.apps-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 32px;
  padding: 0 24px;
}

.app-card {
  border-radius: 12px;
  transition: all 0.3s ease;
  height: 100%;
  border: 1px solid #e8e8e8;
  background: white;
  cursor: pointer;
}

.app-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
}

.app-icon {
  font-size: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  background: rgba(24, 144, 255, 0.1);
  border-radius: 50%;
  margin: 0 auto;
}

.footer {
  text-align: center;
  background: transparent;
  padding: 24px;
}

@media (max-width: 768px) {
  .header-content {
    padding: 20px 24px;
  }

  .main-title {
    font-size: 36px !important;
  }

  .main-description {
    font-size: 18px !important;
  }

  .apps-grid {
    grid-template-columns: 1fr;
    gap: 24px;
    padding: 0;
  }

  .content {
    padding: 40px 16px;
  }

  .intro-section {
    margin-bottom: 40px;
    padding: 0;
  }
}

@media (max-width: 480px) {
  .main-title {
    font-size: 28px !important;
  }

  .main-description {
    font-size: 16px !important;
  }
}

